.skills ul {
  display: flex;
  justify-content: center;
  width: fit-content;
  border-radius: 8px;
  padding: 0;
  margin: auto auto 3% auto;
  background-color: rgba(199, 199, 199, 0.644);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.skills ul li {
  margin: 1%;
  height: 20vh;
  width: 20vh;
  padding: 1%;
}

.skills ul li:hover {
  background-color: #fff5eb6b;
}

@media only screen and (max-width: 650px) {
  .skills ul {
    width: 90%;
  }
  .skills ul li {
    height: 2.5rem;
    width: 2.5rem;
  }
}
